.close_button {
  all: unset;
  font-size: 12px;
  cursor: pointer;
}

.add_timetable_button {
  all: unset;
  font-size: 12px;
  cursor: pointer;
  border: 2px solid #77808d;
  border-radius: 6px;
  padding: 6px 28px;
  &:hover {
    background-color: #77808d;
    color: white;
  }
}