@use 'src/styles/mixins';
.date_range {
  @include mixins.flex(row, center);
  position: relative;
  flex: 0 1 40%;
  input[type='date'] {
    text-align: center;
    font-size: 12px;

  }
  .start_date {
    border-right: none;
    border-radius: 6px 0 0 6px;
  }
  .end_date {
    border-left: none;
    border-radius: 0 6px 6px 0;
  }

  .until {
    @include mixins.flex(row, center, center);
    color: white;
    background-color: #bdbdbd;
    padding: 10px;
    font-size: 12px;
  }
}

